import React from 'react'
import Navbar from '../components/Navbar'

export default function About() {
    return (
        <>
        <Navbar/>
        <div>
                The data that is used for testing the Dashboard was generated using the velocity data that we collected using a sensor data acquisition tool. This velocity data using the sensors of the phone as we went for drives. This data is then logged and was converted to the suitable format so that the postgreSQL database could be updated.

                Using the velocity data, further more information such as acceleration and other parameters were determined. For producing data for parameters such as battery voltage and SOC, we used random initial conditions complying with the general physical constraints of real life usage. The power usage was calculated using the acceleration data. The mass of the vehicle combined with the driver was assumed to be 100 kg for the calculation. The regenerative braking efficiency was assumed to be 0.45 throughout for simplification. 
                 
                All these manipulations were done because in the current situation we cannot collect data using the traditional method of driving an EV and logging data from it.
                 
                All the generated data was made consistent with the real life situations. Charging and Discharging parameters were manually added to the data file.
                 
                Later this data is transferred to the Remote postgreSQL database using TCP IP protocol whenever the data logger (our mobile phones in this case for testing purposes) is connected through the internet with a bandwidth suiting atleast 100 kbps is available. 
                 
                The data was collected for 3 days, for 3 different vehicle IDs, and for each day data for about 9 minutes was generated. The data collection frequency was set to 1Hz for ease. This frequency can also be made higher, but this would not affect the system in any way.
                 
                MATLAB codes and filters were used to remove and eliminate the outliers from the collected data.
        </div>
        </>
    )
}
