import React,{useRef, useState} from 'react'
import { Button } from 'react-bootstrap'
import {useAuth} from '../../contexts/AuthContext'
import { Link } from 'react-router-dom'
import CenteredContainer from './CenteredContainer'
import ather2 from '../../img/ather2.png'
import spinner from '../../img/black-spinner.gif'


export default function Login()
{
    const emailRef = useRef()
    const { resetPassword } = useAuth();
    const [message, setMessage] = useState('')
    const [loading, setLoading] = useState(false)

    const handleSubmit = async (e) =>
    {        
        e.preventDefault()
        try
        {
            setMessage('')
            setLoading(true)
            await resetPassword(emailRef.current.value)
            console.log('Login Successful !')
            setMessage('Password reset email sent')
        } catch (error) {
            console.log(error.message)
            setMessage('Failed to reset password')
        }
        setLoading(false)
    } 


    return (
        <CenteredContainer>
            <form className="form-container">
                    <div className="auth-heading">
                        <img src={ather2} alt="ather-logo"/>
                        <h2>Password Reset</h2>
                    </div>
                    {message && <p className="danger" >{message}</p>}
                    <form onSubmit={handleSubmit} >
                            <label htmlFor="email">Email</label>
                            <input type="email" input="email" ref={emailRef} required >
                            </input>
                            <Button disabled={loading} className="authen-button" type="submit">{!loading ? "Submit" : <img alt="ather-logo" src={spinner} style={{height:"16px", width:"16px"}} />}</Button>
                    </form>
            </form>
            <div className="alternate-auth">
                Need an account ? <Link className="alternate-auth-link" to="/signup">Sign Up.</Link>
            </div>      
        </CenteredContainer>
    )
}

